<script>
import axios from 'axios'

export default {
  props: {
  },
  components: {
  },
  data() {
    return {
      goodsAllList : [],
    }
  },
  created : function () {
  },
  computed : {
    openIcon : function () {
      return (index) => {
        if (this.dispDataList[index].active == true) {
          return "minus";
        } else {
          return "plus";
        }
      }
    },
  },
  methods : {
    pushMeru : function () {
      window.open("https://jp.mercari.com/search?afid=0750724495&page_token=v1%3A1&search_condition_id=1cx0zHHN0HTEcax3jgrnjg4jjgrDjg6k");
    },
    pushTop : function () {
      window.scrollTo(0, 0);
    },
    open : function(index) {
      let flg = this.dispDataList[index].active;
      this.dispDataList[index].active = !flg;
      // console.log(flg);
    },
    initAction : function () {
      this.$root.pageAction("GOODS");
      this.getList(true);
    },
    getList : function () {
      this.$root.showLoad(); 

      let params = new URLSearchParams();
      axios.post(this.$root.SERVER_PATH + "getGoodsList", params)
      .then((response) => {
        let resData = response.data;
        // console.log(resData);
        let goodsList = resData["goodsList"];

        this.goodsAllList = goodsList;
        for (let i = 0; i < this.goodsAllList.length; i++) {
          const element = goodsList[i];
          element.url = element.url.replace("https://jp.mercari.com/item/", "");
        }
        this.$root.closePopup();
      });
    }
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.initAction();
    });
  },
  beforeRouteUpdate(to, from, next) {
    next(vm => {
      vm.initAction();
    });
  },
  watch : {
  }
}
</script>

